<template>
  <div class="main-container">
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>内容阅读</el-breadcrumb-item>
      <el-breadcrumb-item>{{ detail.title }}</el-breadcrumb-item>
    </el-breadcrumb>
    <el-row v-if="loading" style="text-align: center;">
      <el-button type="text" class="load-icon" disabled><i class="el-icon-loading" /></el-button>
    </el-row>
    <div class="mtop12">
      <el-collapse v-model="activeNames">
        <el-collapse-item name="1">
          <template slot="title">
            <span class="title">{{ detail.title }}</span>
          </template>
          <div v-html="detail.content" />
        </el-collapse-item>
      </el-collapse>
    </div>
  </div>
</template>

<script>
import { debug } from '@/utils'
import { view } from '@/api/article'
import getPageTitle from '@/utils/get-page-title'

export default {
  name: 'ViewMaterial',
  data() {
    return {
      loading: true,
      activeNames: ['1'],
      detail: {
        id: 0,
        title: '',
        content: ''
      }
    }
  },
  created() {
    this.detail.id = this.$route.params.id
    this.getDetail()
  },
  methods: {
    getDetail() {
      view({ id: this.detail.id }).then(response => {
        if (response.status === 'success') {
          document.title = getPageTitle(response.data.title)
          this.detail.id = response.data.id
          this.detail.title = response.data.title
          this.detail.content = response.data.content
        }
        this.loading = false
      }).catch(error => {
        this.loading = false
        debug(error)
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.title {
  font-weight: bold;
  width: 100%;
  font-size: 24px;
  float: left;
  white-space: nowrap;
  overflow: auto;
}
.el-row {
  margin-bottom: 20px;
  &:last-child {
    margin-bottom: 0;
  }

  .load-icon {
    font-size: 28px;
    color: #389cd6;
  }
  .text {
    font-size: 14px;
  }
}
</style>
