import request from '@/utils/request'

export function getList(data, url) {
  if (url === undefined) {
    url = '/material'
  }
  return request({
    url: url,
    method: 'get',
    params: data
  })
}

export function view(data) {
  return request({
    url: '/view',
    method: 'get',
    params: data
  })
}
